document.addEventListener('DOMContentLoaded', function () {
    const hairTypes = [
        "<p>Włosy suche i lekko zniszczone</p>",
        "<p>Do włosów długich</p>",
        "<p>Do włosów koloryzowanych i rozjaśnianych</p>",
        "<p>Do każdego rodzaju włosów</p>",
        "<p>Do włosów kręconych</p>",
    ];
    const spoils = [
        "<p>Regeneruje i odżywia</p>",
        "<p>Oczyszcza, odbudowuje i poprawia wygląd włosów na długościach<sup>2</sup></p>",
        "<p>Oczyszcza i przedłuża trwałość koloru do 8 tygodni<sup>2</sup> i przywraca blask</p>",
        "<p>97% mniej złamanych włosów już przy pierwszym użyciu<sup>2</sup>, długotrwały kolor<sup>3</sup>.</p>",
        "<p>Oczyszcza, daje 2x więcej nawilżenia<sup>2</sup> i podkreśla loki</p>",
    ];
    const ingredients = [
        "<p>Ekstrakty z otrębów, złotej komosy ryżowej, białko pszeniczne</p>",
        "<p>Filler A-100 i aminikwasy</p>",
        "<p>Resveratrol</p>",
        "<p>Ekstrakty z otrębów, złotej komosy ryżowej, białko pszeniczne</p>",
        "<p>Gliceryna, hydroksyetylomocznik, nasiona hibiskusa</p>",
    ];
    const desc = [
        "<p></p>",
        "<p>[2] Test instrumentalny: Pro Longer szampon + odżywka.</p>",
        "<p>[2] Test instrumentalny: szampon + odżywka.</p>",
        "<p>[2] Test instrumentalny.<br/>[3] Zachowanie koloru po wielokrotnym zastosowaniu szamponu i maski Metal Detox.</p>",
        "<p>[2] Test instrumentalny: szampon nawilżający + bogata maska.</p>",
    ]

    let currentSlide = 0;
    let currentSlideMobile = 0;
    let currentSlideFooter = 0;


    const slides = document.querySelectorAll("#carousel .rc-slides-container img");
    const slidesMobile = document.querySelectorAll("#carousel-mobile .rc-slides-container img");
    const slidesFooter = document.querySelectorAll("#carousel-footer .rc-slides-container img");
    const slidesFooterName = document.querySelectorAll("#carousel-footer .rc-slides-container-name div");

    const prevBtn = document.getElementById("prev-btn");
    const nextBtn = document.getElementById("next-btn");
    const prevBtnMobile = document.getElementById("prev-btn-mobile");
    const nextBtnMobile = document.getElementById("next-btn-mobile");
    const prevBtnFooter = document.getElementById("prev-btn-footer");
    const nextBtnFooter = document.getElementById("next-btn-footer");


    function showString(typesArray, spoilsArray, ingredientsArray, descArray, index) {
        const types = typesArray[index];
        const spoils = spoilsArray[index];
        const ingredients = ingredientsArray[index];
        const desc = descArray[index];
        document.getElementById("typeHair").innerHTML = types;
        document.getElementById("spoils").innerHTML = spoils;
        document.getElementById("ingredients").innerHTML = ingredients;
        document.getElementById("footer-desc").innerHTML = desc;
    }


    function showSlide(index, indexMobile, indexFooter) {
        slides.forEach((slide) => {
            slide.style.transform = `translateX(-${index * 100}%)`;
        });
        slidesMobile.forEach((slide) => {
            slide.style.transform = `translateX(-${indexMobile * 100}%)`;
        });
        slidesFooter.forEach((slide) => {
            slide.style.transform = `translateX(-${indexFooter * 100}%)`;
        });
        slidesFooterName.forEach((slide) => {
            slide.style.transform = `translateX(-${indexFooter * 100}%)`;
        });
    }


    function prevSlide() {
        currentSlide = (currentSlide - 1 + slides.length) % slides.length;
        currentSlideMobile = (currentSlideMobile - 1 + slidesMobile.length) % slidesMobile.length;
        currentSlideFooter = (currentSlideFooter - 1 + slidesFooter.length) % slidesFooter.length;
        showSlide(currentSlide, currentSlideMobile, currentSlideFooter);
        showString(hairTypes, spoils, ingredients, desc, currentSlideFooter);

    }

    function nextSlide() {
        currentSlide = (currentSlide + 1) % slides.length;
        currentSlideMobile = (currentSlideMobile + 1) % slidesMobile.length;
        currentSlideFooter = (currentSlideFooter + 1) % slidesFooter.length;
        showSlide(currentSlide, currentSlideMobile, currentSlideFooter);
        showString(hairTypes, spoils, ingredients, desc, currentSlideFooter);

    }

    prevBtn.addEventListener("click", prevSlide);
    nextBtn.addEventListener("click", nextSlide);
    prevBtnMobile.addEventListener("click", prevSlide);
    nextBtnMobile.addEventListener("click", nextSlide);
    prevBtnFooter.addEventListener("click", prevSlide);
    nextBtnFooter.addEventListener("click", nextSlide);

    const videos = document.querySelectorAll('.rc-myVideo');
    videos.forEach(video => {
        video.addEventListener('touchstart', () => {
            if (video.paused) {
                video.play();
            } else {
                video.pause();
            }
        });
        video.addEventListener('mouseenter', () => {
            video.play();
        });

        video.addEventListener('mouseleave', () => {
            video.pause();
        });
    })
});